.mainContainer {
    margin-top: 50px;
    margin-bottom: 60px;
}

.poster {
    width: 100%;
    max-width: 360px;
    min-width: 180px;
}

.moviesCell {
    cursor: pointer;
    display: flex;
    flex-direction: row;
}

.moviesCell-text {
    padding: 15px;
    align-self: center;
    flex-basis: 70%;
    flex-grow: 4;
}

.moviesCell-img {
    flex-basis: 30%;
    flex-grow: 1;
}

iframe {
    margin: 0 auto;
    display: block;
    width: 100%;
}

